@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;700&display=swap);
/* https://fonts.google.com/specimen/Mulish#standard-styles */

body {
  font-family: 'Mulish', sans-serif;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 24px;
}

p {
  font-size: 16px;
}

ul {
  list-style: none;
  padding: 0;
}

/* block__element--modifier */

.Directions__list li {
  padding-bottom: 8px;
}

.Footer {
  align-items: center;
  background-color: #7b68a1;
  color: white;
  display: flex;
  font-size: 12px;
  height: 128px;
  justify-content: center;
}

.Header {
  background-color: #f7972e;
  height: 64px;
}

.Ingredients__list {
  list-style: disc;
  margin-left: 24px;
}

.Ingredients__list li {
  padding-bottom: 4px;
}

.Meta li {
  padding-bottom: 4px;
}

.Nav {
  padding: 0 32px;
}

.Recipe {
  padding: 0 32px;
}

.Recipe__hero {
  border: 1px solid lightgray;
  width: 100%;
}

